<template>
  <div id="edit-employee">
      <h3>Edit Employee</h3>

      <div class="row">
      	<form @submit.prevent="updateEmployee" class="col s12">
      		<div class="row">
      			<div class="input-field col s12">
      				<input disabled
      			</div>
      		</div>

      		<div class="row">
      			<div class="input-field col s12">
      				<input type="text" v-model="name" required>
      			</div>
      		</div>

      		<div class="row">
      			<div class="input-field col s12">
      				<input type="text" v-model="img" required>
      			</div>
      		</div>

      		<div class="row">
      			<div class="input-field col s12">
      				<input type="text" v-model="dept" required>
      			</div>
      		</div>

      		<div class="row">
      			<div class="input-field col s12">
      				<input type="text" v-model="position" required>
      			</div>
      		</div>

      		 <router-link class="btn grey" to="/">Cancel</router-link>
      		 <button class="btn" type="submit">Save Changes</button>
      	</form>
      </div>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'view-employee',
  data () {
      return {
          Employee_id: null,
          name: null,
          img: null,
          dept: null,
          position: null
      }
  },
  beforeRouteEnter (to, from, next){
  	db.collection('employees').where('employee_id', '==', to.params.employee_id).get()
  		.then(querySnapshot => {
  			querySnapshot.forEach(doc => {
  				next(vm => {
  					vm.employee_id = doc.data().employee_id
  					vm.name = doc.data().name
  					vm.img = doc.data().img
  					vm.dept = doc.data().dept
  					vm.position = doc.data().position
  				})
  			})
  		})
  },
   watch: {
   	'$route': 'fetchData'
   },
   methods: {
   	fetchData () {
   		db.collection('employees').where('employee_id', '==', this.$route.params.employee_id).get()
   		.then(querySnapshot => {
   			querySnapshot.forEach(doc => {
   				this.employee_id = doc.data().employee_id
   				this.name = doc.data().name
   				this.img = doc.data().img
   				this.dept = doc.data().dept
   				this.position = data().position
   			})
   		})
   	},
   	updateEmployee () {
   		db.collection('employees').where('employee_id', '==', this.$route.params.employee_id).get()
   		.then(querySnapshot => {
   			querySnapshot.forEach(doc => {
   				doc.ref.update({
   					employee_id: this.employee_id,
   					name:  this.name,
   					img: this.img,
   					dept: this.dept,
   					position: this.position
   				})
   				.then(() => this.$router.push({
   					name: 'view-employee',
   					params: {employee_id: this.employee_id}
   				}))
   			})
   		})
   	}
   }
}
</script>




